.AudioCard {
  width: 300px;
  height: 450px;
  background: white;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 15px;
  overflow: hidden;
  margin-bottom: 1rem;
  position: relative;
  margin-left: 50px;
  margin-right: 50px;
}

.AudioCard p {
  position: absolute;
  width: 100%;
  z-index: 2;
  text-align: center;
}

.AudioCard-img {
  width: inherit;
  height: auto;
  object-fit: cover;
}

.Audiobook-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  -webkit-padding-start: 0 !important;
          padding-inline-start: 0 !important;
}

.btn {
  padding: 0 !important;
}

.Modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Modal__container {
  border-radius: 15px;
  display: grid;
  overflow: hidden;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 3.8fr 1fr;
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  width: 400px;
  height: 600px;
  z-index: 2;
}

.Modal__close-button {
  z-index: 2;
  position: absolute;
  font-weight: bolder;
  border-radius: 15px;
  top: 0;
  right: 0;
  border: 0;
  background-color: #ffffff00;
  padding: 0.2rem 1rem;
}

.Modal__container p {
  text-align: center;
  z-index: 2;
}

.Modal__title {
  grid-row: 1;
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
  margin-top: 30px;
  z-index: 2;
}

.Modal__duration {
  grid-row: 6;
  align-self: flex-end;
  font-weight: bold;
}

.Modal__isOriginal {
  color: aquamarine;
  font-weight: bold;
}
.Modal__authors {
}

.Modal__cover-img {
  top: 0;
  opacity: 0.8;
  width: 100%;
  height: auto;
  display: flex;
  position: absolute;
}

.Modal__btn-container {
  color: azure;
  grid-row: 7;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.Modal__btn-container Button {
  height: 40px;
  margin-left: 10px;
  margin-right: 10px;
  z-index: 2;
}
.Modal__link {
  color: azure;
  text-align: center;
  text-decoration: none !important;
}

.Modal__link:hover {
  color: azure;
  margin: auto;
}

.Delete__modal-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Delete__container {
  border-radius: 15px;
  display: grid;
  overflow: hidden;
  grid-template-rows: 1fr 1fr;
  position: relative;
  top: 10rem;
  background-color: #ffffff;
  width: 400px;
  height: 200px;
  z-index: 2;
  padding: 20px;
  text-align: center;
}

.Delete__buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.Delete__buttons Button {
  height: 40px;
  margin-left: 10px;
  margin-right: 10px;
  z-index: 2;
}

.lds-grid {
  position: relative;
  margin: auto;
  width: 64px;
  height: 64px;
}

.lds-grid div {
  position: absolute;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation: lds-grid 1.2s linear infinite;
          animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
  top: 6px;
  left: 6px;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
  top: 6px;
  left: 26px;
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
  top: 6px;
  left: 45px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
  top: 26px;
  left: 6px;
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
  top: 26px;
  left: 26px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
  top: 26px;
  left: 45px;
  -webkit-animation-delay: -1.2s;
          animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
  top: 45px;
  left: 6px;
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
  top: 45px;
  left: 26px;
  -webkit-animation-delay: -1.2s;
          animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
  top: 45px;
  left: 45px;
  -webkit-animation-delay: -1.6s;
          animation-delay: -1.6s;
}

@-webkit-keyframes lds-grid {
  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

@keyframes lds-grid {
  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }
}

.main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 300px;
  border-radius: 0 0 40px 40px;
}

.main__title {
  color: white;
  font-size: 25px;
}

.input {
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid white;
  border-radius: 25px;
  color: white;
  font-family: "Muli", sans-serif;
  font-size: 16px;
  height: 50px;
  margin-bottom: 20px;
  outline: none;
  padding: 0px 20px;
  width: 70%;
}

.Audiobooks-container {
  display: inline-block;
  width: 100%;
}

.Navbar {
  width: 100%;
  height: 50px;
  display: inline-block;
  /* border-bottom: 3px solid #959b95; */
}

.Navbar a {
  font-size: 1.5em;
}

.Navbar__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.Navbar__admin {
  grid-column: 2;
  margin-top: 15px;
  justify-self: right;
  margin-right: 0.5rem;
  text-decoration: none;
}

.Navbar__admin a {
  color: white;
  text-decoration: none;
}

.Navbar__admin a:hover {
  text-decoration: none;
}

.Navbar__account {
  cursor: pointer;
  margin-left: 15px;
  width: 35px;
  height: auto;
  color: white;
}

.Navbar__brand-logo {
  width: 70px;
  margin-right: 0.5rem;
}
.Navbar__brand {
  color: #ffffff;
  display: flex;
  align-items: center;
  margin-bottom: 100px;
}

.Navbar__brand:hover {
  color: #ffffff;
  text-decoration: none;
}
.Navbar__brand-logo {
}

.form-container {
  margin-top: 50px;
  color: white;
}

.form-container .button-send {
  width: 80px;
  height: 40px;
  background-color: #36d7dc;
}

.checkbox {
  width: 50px !important;
}

.container {
  display: grid;
  align-content: center;
}

.NotFound__container {
  width: 100%;
  display: grid;
  align-content: center;
  justify-content: center;
  color: azure;
  text-align: center;
}

.NotFound__container img {
  width: 100px;
  margin: auto;
}

